<template>
  <div class="container">
    <h1> Latest Contacts</h1>
    <div class="create-contact">
      <label for="create-contact">Input the bottom fields</label>
      <br>
      <input type="text" id="create-contact" v-model="firstName" placeholder="First name">
      <br>
      <input type="text" id="create-contact" v-model="lastName" placeholder="Last name">
      <br>
      <input type="text" id="create-contact" v-model="address" placeholder="Address">
      <br>
      <input type="text" id="create-contact" v-model="email" placeholder="E-mail">
      <br>
      <input type="text" id="create-contact" v-model="phoneNumber" placeholder="Phone number">
      <br>
      <button @click="createContact">Create contact</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="contacts-container">
      <div class="contact"
        v-for="(contact, index) in contacts"
        v-bind:item="contact"
        v-bind:index="index"
        v-bind:key="contact._id"
        v-on:dblclick="deleteContact(contact._id)"
        >
        {{ `${contact.createdAt.getDate()}/${contact.createdAt.getMonth()}/${contact.createdAt.getFullYear()}`}}
        <p class="text">{{ contact.firstName }}</p>
        <p class="text">{{ contact.lastName }}</p>
        <p class="text">{{ contact.address }}</p>
        <p class="text">{{ contact.email }}</p>
        <p class="text">{{ contact.phoneNumber }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import ContactsService from '../ContactsService'
export default {
  name: 'ContactsComponent',
  data() {
    return {
      contacts: [],
      error: '',
      firstName: '',
      lastName: '',
      address: '',
      email: '',
      phoneNumber: ''
    }
  },
  async created() {
    try {
      this.contacts = await ContactsService.getContacts();
    } catch (err) {
      this.error = err.messsage;
    }
  },
  methods: {
    async createContact() {
      await ContactsService.insertContact(this.firstName, this.lastName, this.address, this.email, this.phoneNumber);
      this.contacts = await ContactsService.getContacts();
    },
    async deleteContact(id) {
      await ContactsService.deleteContact(id);
      this.contacts = await ContactsService.getContacts();
    }
  }
}
</script>

